import React, {ReactNode, useState} from 'react';
import {Button, Input, InputGroup, InputGroupText, FormGroup} from 'reactstrap';
import {X, Search as SearchIcon} from 'lucide-react';

type SearchProps = {
    searchText: string | null;
    onSearch: (searchText: string) => void;
    children?: ReactNode;
};

const Search = ({searchText, onSearch, children}: SearchProps) => {
    const [text, setText] = useState(searchText || '');

    const onClear = () => {
        setText('');
        onSearch('');
    };

    const searchIfEnter = (event: React.KeyboardEvent<HTMLInputElement>) => {
        if (event.key === 'Enter') {
            onSearch(text);
        }
    };

    return (
        <div className="d-flex justify-content-between align-items-end">
            <div className="d-flex align-items-end">
                <Button
                    id="ClearSearch"
                    color="link"
                    className="p-0 me-2"
                    onClick={onClear}
                >
                    <X />
                </Button>
                <FormGroup>
                    <InputGroup>
                        <Input
                            type="text"
                            placeholder="Search..."
                            aria-label="Search"
                            aria-describedby="SearchButton"
                            value={text}
                            onChange={(
                                event: React.ChangeEvent<HTMLInputElement>
                            ) => setText(event.target.value)}
                            onKeyDown={searchIfEnter}
                        />
                        <InputGroupText>
                            <Button
                                color="primary"
                                id="SearchButton"
                                onClick={() => onSearch(text)}
                            >
                                <SearchIcon />
                            </Button>
                        </InputGroupText>
                    </InputGroup>
                </FormGroup>
            </div>

            {children && <span>{children}</span>}
        </div>
    );
};

export default Search;
